import 'core-js/stable';
import 'regenerator-runtime/runtime';
import { version } from '../package.json';
import './assets/styles/class.css';
import './assets/styles/font.css';
import getMetaData from './api';
import { parse } from 'querystring';
import { postMessage, preloadFonts, preloadModules, SOL } from 'sol-common/util';
import { createLoader, preloadMP3, preloadPNGImages, preloadSVGImages } from 'sol-common';
import Snap from 'sol-common/snap';

console.log('D1: v' + version);

const getFrameId = () => parse(location.search.replace('?', '')).frame_id;

const setPreloadData = async ({ meta, fonts }) => {
    const loader = createLoader();
    preloadMP3(meta);
    await Promise.all([preloadPNGImages(meta), preloadSVGImages(meta), preloadFonts(fonts), preloadModules(meta)]);
    loader.remove();
};
const setReceiveMessage = () => {
    const message = {
        forceFrameEnd: '00'
    };

    const methods = {
        [message['forceFrameEnd']]: () => clearMain()
    };

    return ({ data }) => {
        const { code, param } = data;
        methods[code]?.(param);
    };
};

const getPostMessageData = data => {
    const message = {
        frameLoadEnd: '00',
        frameEnd: '01'
    };

    return {
        code: message[data]
    };
};

const setController = metaData => {
    let tryCount = 3;
    let actions = []; //0:오반응 1:정반응
    let n = 0;

    const run = async () => {
        const meta = metaData[n];
        initActions();

        SOL.modules[meta.questionType]({
            ...meta,
            canvas: SOL.SVG,
            callback: () => next()
        });
    };
    const addUserAction = action => {
        if (typeof action === 'boolean') action = Number(action);
        actions.push(action);
        const lastFeedbackActions = actions.filter(el => el === 0 || el === 1).slice(-tryCount);
        const isConsecutiveIncorrect = lastFeedbackActions.length >= tryCount && lastFeedbackActions.every(el => el === 0);

        isConsecutiveIncorrect && SOL.controller.next();
    };
    const next = async () => {
        n += 1;
        clearMain();
        if (metaData[n]) {
            run();
        } else {
            const poseMessageData = getPostMessageData('frameEnd');
            postMessage(poseMessageData);
        }
    };
    const initActions = () => (actions = []);

    return { run, next, addUserAction, initActions, actions };
};

// const clearContents = () => SOL.SVG.children().forEach(el => el.hasClass('D1-contents') && el.remove());

export const clearMain = () => {
    const main = document.getElementById('main');
    let lastEl = main.lastChild;
    let firstEl = main.firstChild;
    while (lastEl !== SOL.SVG.node) {
        lastEl.remove();
        lastEl = main.lastChild;
    }
    while (firstEl !== SOL.SVG.node) {
        firstEl.remove();
        firstEl = main.firstChild;
    }
    SOL.SVG.clear();
};

const start = async () => {
    const frameId = getFrameId();    
    const metaData = await getMetaData(frameId);

    const receiveMessage = setReceiveMessage();
    window.addEventListener('message', receiveMessage);

    SOL.SVG = Snap('#frame2d');
    await setPreloadData({ meta: metaData, fonts: ['ffgt', 'dn53'] });
    SOL.controller = setController(metaData);
    const poseMessageData = getPostMessageData('frameLoadEnd');
    postMessage(poseMessageData);
    SOL.controller.run();
};

start();
